	module.exports = {"data":{"markdownRemark":{"html":"<p>上课，讲西方政治思想的分期问题。我的方法是用方法论或者说世界观来分。从古代到现代，分出了伦理学世界观、神学世界观、法学世界观、社会学世界观四种主要的方法论，从而把西方政治思想分为</p>\n<p>四个主要的阶段。在每一个阶段均有一种主导的方法论，这种方法论主导了一定时期的政治思维和一般的思维方式。这种方法论是主观的，也是客观的。就是说个人不容易超脱这样的宏观逻辑。尽管一定时期的思想家在不同的政治问题上可以有不同的观点，但是在方法论上是很难脱离总的世界观的。因为人总是一定时代的产物。这样的一种分法，是一家，还有其他的研究方法。</p>\n<p>到日本中心去，安排日本松下公司的顾问来讲演的问题。现在好像外国人来得太多了，要为一次讲座找到一些听众十分不容易。也可能是不少人对知识无兴趣，或者是外国人不能讲到中国人能理解的程度。但是，我发现所有的外国教授，除了少数，演讲总是不行，没有内容和逻辑，太浅。也许他们认为我们就懂这一点。要不然，就是中国大学的教学水平高于他们。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0048.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0048.md","frontmatter":{"title":"3月3日 星期四"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0048.md absPath of file >>> MarkdownRemark"}}

